.navContainer {
    height: 45px;
    background-color: color(nav-bg);
    backdrop-filter: blur(3px);
    margin: 20px;
    border-radius: 10px;
    z-index: 100;
  }
  
  .landingBody {
  
    margin: 0;
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
  
    .landingCard {
      scroll-snap-align: start;
    }
  }

  
  
  @media only screen and (max-width: 767px) {
    
    #mobileNav {
      height: 130px;
      // background-color: #f1f1f1;
      background-color: color(nav-bg);
      backdrop-filter: blur(3px);
      padding: 5px;
      margin-top: 5px;
      margin-left: -15px;
      margin-right: -15px;
      border-radius: 10px;
      z-index: 100;
    }
  
    #navIcon {
      height: 32px;
      margin-top: -10px;
      display: flex;
      align-self: center;
    }

    .navLinks {
      font-family: 'Heebo';
      font-weight: 700;
      color: color(black);
      text-align: center;
    }
  }

  #navIcon {
    height: 32px;
    padding-bottom: 0px;
    margin-left: 10px;
  }
  
  .navbarTogglerIcon {
    margin-bottom: 10px;
    border-width: 0px;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
  
    #navToggle {
      border: none;
      color: color(accent);
      font-size: 22px;
      
    }
  }
  
  #mainNav {
    margin-right: 0px;

    .active {
      color: color(accent);
    }
  
    .navItem {
      margin-left: 10px;
      padding-right: 10px;
      padding-left: 10px;
    
      .navLinks {
        font-family: 'Heebo';
        font-weight: 600;
        color: color(black);
        transition: all 0.3s ease-in-out;
      }

      .navbar__link {
        font-family: 'Heebo';
        font-weight: 600;
        color: color(black);
        transition: all 0.3s ease-in-out;
      }

      .navbar__link--active {
        border-bottom: 3px solid #29b6f6;
        transition: border-bottom .5s ease-in-out;
      }

      .navLinks:hover {
        color: color(accent);
      }

      .navLinks:target {
        color: color(accent);
      }

    }
  
  }

  a:link {
    text-decoration: none;
  }
  
  a:visited {
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: none;
  }

  @media not all and (min-resolution:.001dpcm) and (max-width: 767px)
  { @supports (-webkit-appearance:none) {
  
    #navIcon {
      height: 32px;
      margin-top: 0px;
      display: flex;
      align-self: center;
    }
    
    .navbarTogglerIcon {
      margin-bottom: 0px;
    }

  }}

